<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
  <div>
    <v-card>
      <toolbar-list :info="infoComponent" title="Anticipos" subtitle="Lectura y gestión"/>
      <loading v-model="tableLoading" />
      <v-container fluid>
        <v-layout row wrap>
          <v-flex xs1 sm3 md6 class="text-xs-right">
            <v-tooltip top>
              <v-btn
                slot="activator"
                flat
                icon
                color="accent"
                @click="reloadPage"
              >
                <v-icon>cached</v-icon>
              </v-btn>
              <span>Actualizar registros</span>
            </v-tooltip>
          </v-flex>
          <v-flex xs12 sm3 md2>
            <v-select
              label="Registros por página"
              v-model="pagination.per_page"
              :items="items_page"
              item-text="text"
              item-value="value"
            />
          </v-flex>
          <v-flex xs12 sm6 md4>
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Buscar"
              @input="buscar"
            />
          </v-flex>
        </v-layout>

        <v-data-table
          :headers="headers"
          :items="anticipos"
          :loading="tableLoading"
          :expand="expand"
          :pagination.sync="pagination"
          :total-items="anticipos.length"
          :search="search"
          hide-actions
          class="elevation-1">
          <template slot="items" slot-scope="props">
            <tr @click="props.expanded = !props.expanded">
              <td>{{ props.item.consecutivo  }}</td>
              <td>{{ props.item.tercero.nombre_completo }}</td>
              <td>{{ props.item.tercero.identificacion }}</td>
              <td> <span v-html="props.item.cuenta.codigo_nombre"></span></td>
              <td>{{ props.item.fecha_documento}}</td>
              <td>{{ props.item.valor_formateado}}</td>
              <td>{{ props.item.origen}}</td>
              <td>{{ props.item.saldo_incial.consecutivo}}</td>
              <td>
                <v-speed-dial
                  v-model="props.item.show"
                  direction="left"
                  open-on-hover
                  transition="slide-x-transition"
                >
                  <v-btn
                    slot="activator"
                    v-model="props.item.show"
                    color="blue darken-2"
                    dark
                    fab
                    small
                  >
                    <v-icon>chevron_left</v-icon>
                    <v-icon>close</v-icon>
                  </v-btn>
                  <v-tooltip top>
                    <v-btn
                      fab
                      dark
                      small
                      color="white"
                      slot="activator"
                      @click="$store.commit('NAV_ADD_ITEM', { ruta: 'cruceAnticipos', titulo: 'Cruce anticipos', parametros: {entidad: props.item, tabOrigin: $store.state.nav.currentItem.split('tab-')[1]} })"
                    >
                      <v-icon color="accent">call_split</v-icon>
                    </v-btn>
                    <span>Cruzar anticipos</span>
                  </v-tooltip>

                  <v-tooltip top>
                    <v-btn
                      fab
                      dark
                      small
                      color="white"
                      slot="activator"
                      @click="descargarPdf(props.item.id)"
                    >
                      <v-icon color="accent">far fa-file-pdf</v-icon>
                    </v-btn>
                    <span>Abrir PDF</span>
                  </v-tooltip>
                </v-speed-dial>
              </td>
            </tr>
          </template>
          <template slot="expand" slot-scope="props">
            <v-card>
              <v-subheader>Objeto contractual:</v-subheader>
              <v-card-text class="pt-0 pb-1" v-html="'<h2>Funciona</h2>'">
              </v-card-text>
            </v-card>
          </template>
          <template slot="no-data">
            <v-alert  v-if="!tableLoading" :value="true" color="error" icon="warning">
              Lo sentimos, no tenemos registros para mostrar. <v-icon>sentiment_very_dissatisfied</v-icon>
            </v-alert>
            <v-alert v-else :value="true" color="info" icon="info">
              Estamos cargando los registros. <v-icon>sentiment_satisfied_alt</v-icon>
            </v-alert>
          </template>

          <template slot="footer">
            <td colspan="100%" class="text-xs-center">
              <v-pagination v-model="pagination.current_page" :total-visible="7"  :length="pagination.last_page" @input="reloadPage"></v-pagination>
            </td>
          </template>
        </v-data-table>
      </v-container>
    </v-card>
  </div>
</template>

<script>
  import {SNACK_SHOW, SNACK_ERROR_LIST} from '@/store/modules/general/snackbar'
  import Loading from '@/components/general/Loading'

  export default {
    name: 'Anticipos',
    components: {
      ToolbarList: () => import('@/components/general/ToolbarList'),
      Loading
    },
    data () {
      return {
        anticipos: [],
        search: '',
        expand: false,
        loadingPdf: false,
        idSeleccionado: null,
        loadingSubmit: false,
        tableLoading: false,
        items_page: [
          {
            text: 15,
            value: 15
          },
          {
            text: 50,
            value: 50
          },
          {
            text: 100,
            value: 100
          }
        ],
        pagination: {
          per_page: 15,
          current_page: 1
        },

        headers: [
          {
            text: 'Consecutivo',
            align: 'left',
            sortable: false,
            value: 'consecutivo'
          },
          {
            text: 'Razon social',
            align: 'left',
            sortable: false,
            value: 'razon_social'
          },
          {
            text: 'Identificacion',
            align: 'left',
            sortable: false,
            value: 'identificacion'
          },
          {
            text: 'Cuenta',
            align: 'left',
            sortable: false,
            value: 'cuenta'
          },
          {
            text: 'Fecha',
            align: 'left',
            sortable: false,
            value: 'valor'
          },
          {
            text: 'Valor',
            align: 'left',
            sortable: false,
            value: 'valor'
          },
          {
            text: 'Origen',
            align: 'left',
            sortable: false,
            value: 'origen'
          },
          {
            text: 'Saldo/Comprobante',
            align: 'left',
            sortable: false,
            value: 'saldo'
          },
          {
            text: 'Opciones',
            align: 'left',
            sortable: false,
            value: 'id'
          }
        ]
      }
    },
    created () {
      this.reloadPage()
    },
    watch: {
      'pagination.per_page' (value) {
        this.pagination.current_page = 1
        this.reloadPage()
      }
    },
    computed: {
      infoComponent () {
        return this.$store.getters.getInfoComponent('anticipos')
      },
      pages () {
        if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
        return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
      }
    },
    methods: {
      reloadPage () {
        this.tableLoading = true
        this.axios.get('pg_anticipos?per_page=' + this.pagination.per_page + '&page=' + this.pagination.current_page + '&search=%25' + this.search + '%25')
          .then((response) => {
            response.data.meta.per_page = this.items_page.find(page => page.value === parseInt(response.data.meta.per_page)) ? parseInt(response.data.meta.per_page) : -1
            this.pagination = response.data.meta
            this.anticipos = response.data.data
            this.tableLoading = false
          })
          .catch(e => {
            this.tableLoading = false
            this.$store.commit(SNACK_ERROR_LIST, {expression: ' al hacer la busqueda de registros. ', error: e})
            return false
          })
      },
      buscar: window.lodash.debounce(function () {
        this.pagination.current_page = 1
        this.reloadPage()
      }, 500),
      descargarPdf (id) {
        this.loadingPdf = true
        this.axios.get(`firmar-ruta?nombre_ruta=anticipo-pdf&id=${id}`)
          .then(res => {
            window.open(res.data)
            this.loadingPdf = false
            this.loader = false
          })
          .catch(e => {
            this.loadingPdf = false
            this.loader = false
            this.$store.commit(SNACK_SHOW, {msg: 'Error al generar el archivo. ' + e, color: 'danger'})
          })
      }
    }
  }
</script>

<style scoped>
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>



